
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

const refreshKey = ['A','B01','B02','B03','B04','B05','B06','B07','B08','B09','B010']
var key = 0
var loadKey = [
{start: 0,end: 9},
{start: 0,end: 9}
]
var keyValue = 'A'
var initLoaded = false 
function getRefreshKey() {
key++
if(key === refreshKey.length) {
key = 0;
}
keyValue = refreshKey[key]
return true
}
export default {
name: 'Index',
components: {
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Tab,
TabItem,
Swiper,
Marquee,
MarqueeItem,
Panel,
Toast
},
data() {
return {
tabIndex: 0,
swiperList: [{
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg',
title: '送你一次旅行',
fallbackImg: 'https://static.vux.li/demo/3.jpg'
}],
swiperIndex: 0,
marqueeList: [],
list: [






],
moreList: [],
impMoreNews: []
}
},
created() {
this.$jsonp('https://3g.163.com/touch/jsonp/sy/recommend/0-1.html').then( data => {


this.swiperList = data.focus.filter( item => item.addata == null).map(item => {
return {
url: item.link,
img: item.picInfo[0].url,
title: item.title
}
})
this.list = data.list.map(item => {

return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})

this.marqueeList = data.live.map(item => {
return {
title: item.title
}
})
this.impNews = data.news.map(item => {
return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})
initLoaded = true
})
},
methods: {
refresh() {
getRefreshKey()
this.$jsonp('https://3g.163.com/touch/jsonp/sy/recommend/0-9.html', {
miss: '00',
refresh: keyValue
}).then(data => {

this.list = data.list.map(item => {

return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})
this.impNews = data.news.map(item => {

return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})
this.$refs.scroller.finishPullToRefresh();
this.$vux.toast.show('刷新完毕')

})
},
infinite() {
if(!initLoaded) {
this.$refs.scroller.finishInfinite()
return
}
let tabIndex = this.tabIndex
if(tabIndex === 0) {

setTimeout(() => {
this.$jsonp(`https://3g.163.com/touch/jsonp/sy/recommend/${loadKey[tabIndex].start}-${loadKey[tabIndex].end}.html`, {
miss: '00',
refresh: keyValue
}).then(data => {
console.log(data);

var loadlist = data.list.map(item => {

return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})

this.moreList = this.moreList.concat(loadlist)

loadKey[tabIndex].start += 10
loadKey[tabIndex].end = loadKey[tabIndex].start + 9
this.$refs.scroller.finishInfinite()
})
}, 1000);
}else if(tabIndex === 1) {
setTimeout(() => {
this.$jsonp(`https://3g.163.com/touch/jsonp/sy/recommend/${loadKey[tabIndex].start}-${loadKey[tabIndex].end}.html`, {
miss: '00',
refresh: keyValue
}).then(data => {
console.log(data);

var impLoadlist = data.news.map(item => {

return {
src: item.picInfo[0] && item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: item.link
}
})

this.impMoreNews = this.impMoreNews.concat(impLoadlist)

loadKey[tabIndex].start += 10
loadKey[tabIndex].end = loadKey[tabIndex].start + 9
this.$refs.scroller.finishInfinite()
})
}, 1000);
}

},
tabClick(value) {
this.tabIndex = value
}
}
}



